































import Vue, { PropType } from "vue";

export default Vue.extend({
  name: "SimpleKeyboard",
  model: {
    prop: "modelValue",
    event: "update:modelValue"
  },
  props: {
    layout: {
      default: () => [
        "Q W E R T Y U I O P {bksp}",
        "A S D F G H J K L {leeg}",
        "Z X C V B N M {space}"
      ],
      type: Array as PropType<string[]>
    },
    display: {
      default: () => ({
        "{leeg}": "Leeg",
        "{space}": "Spatie",
        "{bksp}": "Delete"
      }),
      type: Object as PropType<Record<string, string>>
    },
    staggered: {
      default: false,
      type: Boolean
    },
    keyboardClass: {
      default: "simple-keyboard",
      type: String
    },
    modelValue: {
      type: String
    },
    defaultValue: {
      default: "",
      type: String
    }
  },
  methods: {
    onChange(input: string) {
      this.$emit("update:modelValue", input);
    },
    onKeyPress(button: string) {
      this.$emit("keypress", button);

      switch (button) {
        case "{leeg}":
          this.onChange("");
          break;
        case "{space}":
          this.onChange(this.modelValue + " ");
          break;
        case "{neg}":
          this.handleNeg();
          break;
        case "{bksp}":
          this.onChange(this.modelValue.slice(0, -1));
          break;
        default:
          this.onChange(this.modelValue + button);
          break;
      }
    },
    handleNeg() {
      if (this.modelValue && this.modelValue.startsWith("-")) {
        this.onChange(this.modelValue.slice(1));
      } else if (this.modelValue === "") {
        this.onChange("-1");
      } else {
        this.onChange(`-${this.modelValue}`);
      }
    },
  }
});
